import React, { Component } from 'react';
import "tailwindcss/dist/base.css";
import "./styles/globalStyles.css";
import { config } from './Config/Config';
import Type3 from './Components/Card/Type3';
import Columncardteamlayout from './Components/Card/Type7';
import Numericalabout from './Components/Countup/Type2';
import VideoCard from './Components/Herocard/Type5';
import HeroCardSmk from './Components/HeroCardSmk/HeroCardSmk';
import WhatWeDo from './Components/SmkAbout/WhatWeDo';
// import OurTeamSmk from './Components/OurTeamSmk';
// import BlogSmk from './Components/BlogSmk';
// import SMKProjects from './Components/SMKProjects';
// import SMKliabraries from './Components/SMKLiabraries';

const parseJSON = resp => (resp.json ? resp.json() : resp);

const checkStatus = resp => {
    if (resp.status >= 200 && resp.status < 300) {
        return resp;
    }
    return parseJSON(resp).then(resp => {
        throw resp;
    });
};

const headers = {
    'Content-Type': 'application/json',
};


class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            NavbarData: [],
        };
    }

    componentDidMount = async () => {
        try {
            const NavbarData = await fetch(`${config.apiurl}/navbars`, {
                method: 'GET',
                headers: headers,
            })
                .then(checkStatus)
                .then(parseJSON);
            this.setState({ NavbarData });
        } catch (error) {
            this.setState({ error });
        }

    };

    render() {
        return (
            <>
                {/* <BlogSmk/> */}
                <HeroCardSmk />
                {/* <SMKProjects/> */}
                {/* <SMKliabraries /> */}
                <VideoCard />
                <WhatWeDo />
                <Numericalabout />
                <Type3 />
                <Columncardteamlayout />
                {/* <OurTeamSmk/> */}

            </>
        )
    }
}

export default App;
